'use client'

import { useState, useEffect } from 'react'
import { TodoistTask, TodoistProject } from '@/lib/types'
import { suggestionsCache, ProjectSuggestion } from '@/lib/suggestions-cache'

interface ProjectSuggestionsProps {
  task: TodoistTask
  projects: TodoistProject[]
  projectHierarchy: any
  onProjectSelect: (projectId: string) => void
}

export default function ProjectSuggestions({ 
  task, 
  projects, 
  projectHierarchy, 
  onProjectSelect 
}: ProjectSuggestionsProps) {
  const [suggestions, setSuggestions] = useState<ProjectSuggestion[]>([])
  const [loading, setLoading] = useState(false)

  const loadSuggestions = async () => {
    if (!task || !projectHierarchy) return

    setLoading(true)
    try {
      const suggestions = await suggestionsCache.generateSuggestions(
        task.id,
        task.content,
        task.description || '',
        projectHierarchy,
        task.projectId
      )
      
      console.log('ProjectSuggestions: Loaded suggestions:', suggestions)
      setSuggestions(suggestions)
    } catch (error) {
      console.error('Error loading suggestions:', error)
      setSuggestions([])
    } finally {
      setLoading(false)
    }
  }

  const refreshSuggestions = async () => {
    if (!task || !projectHierarchy) return

    // Invalidate cache for this task and regenerate
    suggestionsCache.invalidateTask(task.id)
    await loadSuggestions()
  }

  // Load suggestions when task changes
  useEffect(() => {
    loadSuggestions()
  }, [task.id, task.content, task.description])

  const getConfidenceColor = (confidence: number) => {
    if (confidence >= 0.8) return 'text-green-600 bg-green-50 border-green-200'
    if (confidence >= 0.6) return 'text-yellow-600 bg-yellow-50 border-yellow-200'
    return 'text-gray-600 bg-gray-50 border-gray-200'
  }

  const getProjectColor = (projectId: string) => {
    const project = projects.find(p => p.id === projectId)
    if (!project) return '#299fe6'
    
    const colorMap: { [key: string]: string } = {
      'berry_red': '#b8256f',
      'red': '#db4035',
      'orange': '#ff9933',
      'yellow': '#fad000',
      'olive_green': '#afb83b',
      'lime_green': '#7ecc49',
      'green': '#299438',
      'mint_green': '#6accbc',
      'teal': '#158fad',
      'sky_blue': '#14aaf5',
      'light_blue': '#96c3eb',
      'blue': '#4073ff',
      'grape': '#884dff',
      'violet': '#af38eb',
      'lavender': '#eb96eb',
      'magenta': '#e05194',
      'salmon': '#ff8d85',
      'charcoal': '#808080',
      'grey': '#b8b8b8',
      'taupe': '#ccac93'
    }
    return colorMap[project.color] || '#299fe6'
  }

  if (loading) {
    return (
      <div className="bg-white rounded-lg shadow-sm p-4 border border-gray-200">
        <div className="flex items-center space-x-2">
          <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-blue-500"></div>
          <span className="text-sm text-gray-600">Generating project suggestions...</span>
        </div>
      </div>
    )
  }

  if (!suggestions.length && !loading) {
    console.log('ProjectSuggestions: No suggestions, showing debug info')
    return (
      <div className="bg-gray-50 rounded-lg p-4 border border-gray-200">
        <div className="text-sm text-gray-600">
          <div className="font-medium mb-1">Debug: No project suggestions</div>
          <div className="text-xs space-y-1">
            <div>Task: "{task.content}"</div>
            <div>Current Project: {task.projectId}</div>
            <div>Available Projects: {projects.length}</div>
            <div>Hierarchy Available: {projectHierarchy ? 'Yes' : 'No'}</div>
          </div>
        </div>
      </div>
    )
  }

  return (
    <div className="bg-white rounded-lg shadow-sm p-4 border border-gray-200">
      <div className="flex items-center justify-between mb-3">
        <h3 className="text-sm font-medium text-gray-700">
          Suggested Projects
        </h3>
        <button
          onClick={refreshSuggestions}
          className="text-xs text-gray-500 hover:text-gray-700 flex items-center space-x-1"
        >
          <svg className="w-3 h-3" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15" />
          </svg>
          <span>Refresh</span>
        </button>
      </div>
      
      <div className="space-y-2">
        {suggestions.slice(0, 3).map((suggestion, index) => (
          <button
            key={`${suggestion.projectId}-${index}`}
            onClick={() => onProjectSelect(suggestion.projectId)}
            className="w-full text-left p-3 rounded-md border border-gray-200 hover:border-blue-300 hover:bg-blue-50 transition-colors group"
          >
            <div className="flex items-center justify-between mb-1">
              <div className="flex items-center space-x-2">
                <div 
                  className="w-2.5 h-2.5 rounded-full flex-shrink-0"
                  style={{ backgroundColor: getProjectColor(suggestion.projectId) }}
                />
                <span className="font-medium text-gray-900 text-sm">
                  {suggestion.projectName}
                </span>
              </div>
              <span className={`px-2 py-1 text-xs font-medium rounded-md border ${getConfidenceColor(suggestion.confidence)}`}>
                {Math.round(suggestion.confidence * 100)}%
              </span>
            </div>
            <p className="text-xs text-gray-600 group-hover:text-gray-700">
              {suggestion.reasoning}
            </p>
          </button>
        ))}
      </div>
    </div>
  )
}